.main_Container {
    height: 30rem;
    width: 100%;
    margin-top: 15rem;
    border-radius: 15px !important;
    background: #c6ece879;

    .Info_Div {
        width: 100%;
        display: flex;

        >input {
            font-family: 'Noto Serif JP', serif;
            margin: 1rem 1rem;
            width: 30%;
            height: auto !important;
            font-size: 2.5rem;
            border-radius: 50px;
            padding: 1rem 2rem;
            background: rgba(255, 255, 255, 0.643);
            outline: none;
            border: none;
        }
        h1{
            padding: 1rem 2rem;
            font-size: 1.7rem;
            font-family: 'Noto Serif JP', serif;
            height: auto !important;
            margin: 1rem 1rem;
            
        }
        .location_error{
            padding: 1rem 2rem;
            font-size: 2.5rem;
            font-family: 'Noto Serif JP', serif!important;
            margin: 1rem 1rem;
            font-weight: 900;
        }
        .location_error_img{
            margin: 1rem 0rem;
            width: 4.5rem;
            height: 4.5rem;

        }
    }

    .other_Info_Div {
        margin: 0;
        padding: 0;
        width: 100%;
        
        h1{
            margin:1.6rem 0;
            font-size: 1.7rem;
            font-family: 'Rajdhani', sans-serif;
        }
        .weather_condition_img{
            width: 75px;
            height: 75px;
            margin:1rem 1rem;
            +h1{
                font-size: 1.7rem;
                font-weight: 700;
            }
        }

        .Temp_c{
            font-size: 3rem;
            font-weight: 900;
        }
        .Temp_f{
            font-size: 2.5rem;
        }
    }
}





