.container-fluid {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;

    video {
        position: absolute;
        top: -10%;
        left: 0;
        width: 100%;
        height: 130%;
        object-fit: cover;
    }

    .container {
        position: relative;
        z-index: 1;
    }
}